
body {
  background-color: #98BAB2;
}

body,
html {
  margin: 0;
  padding: 0;
}

.hero {
  position: relative;
  text-align: center;
  padding: 0;
  margin: 0;
}

.hero_image {
  width: 100%;
  height: 60vh;
}

.hero-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero .btn {
  color: #140000;
  background-color: #6399B3;
  border: none;
  cursor: pointer;
  display: inline-block;

  width: 10vw;
}

.hero .btn:hover {
  background-color: #4b778d;
}

.emperor {
  width: 2vw;
  height: 2vh;
  color: #524D4C;

}

.tumbleweed {
  width: 2vw;
  height: 2vh;
  color: #DAAC7D;

}

.summer_green {
  width: 2vw;
  height: 2vh;
  color: #98BAB2;

}

.hippie_blue {
  width: 2vw;
  height: 2vh;
  color: #6399B3
}

.hero_overlay_text {
  opacity: 0.5;
  background-color: #ffffff;
  font-size: larger;
  color: #140000;
  position: absolute;
  padding: 2vw;
  top: 20vh;
  left: 65vw;
  border-radius: 15px;
}

.home_text_box {
  width: 25vw;
}

.boxshadow {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
}